<template>
  <v-container fill-height>
    <v-layout row wrap align-center fill-height>
      <v-layout row wrap>
        <!--title-->
        <v-flex xs12>
          <h2 class="primary--text text-center" v-text="title"></h2>
        </v-flex>
        <!--input-->
        <template v-if="!signup">
          <v-flex xs4 offset-xs4>
            <v-text-field
              name="username"
              label="Enter Your Username"
              single-line
              prepend-icon="mdi-school"
              v-model="username"
              @keyup.native.enter="getUserSignUp"
            >
            <!-- :value="username" -->
            <!-- @input.native.stop="updateUsername($event.target.value)" -->
            </v-text-field>
          </v-flex>
          <v-flex xs4 offset-xs4>
            <v-text-field
              name="password"
              label="Enter Your Password"
              single-line
              prepend-icon="mdi-fire"
              type="password"
              v-model="password"
              @keyup.native.enter="getUserSignUp"
            >
            <!-- :value="password" -->
            <!-- @input.native.stop="updatePassword($event.target.value)" -->
            </v-text-field>
          </v-flex>
          <v-flex xs4 offset-xs4>
            <v-text-field
              name="passwordConfirmed"
              label="Enter Your Password Again"
              single-line
              prepend-icon="mdi-fire"
              type="password"
              v-model="passwordConfirmed"
              @keyup.native.enter="getUserSignUp"
            >
            <!-- :value="passwordConfirmed" -->
            <!-- @input.native.stop="updatePassword($event.target.value)" -->
            </v-text-field>
          </v-flex>
          <v-flex xs4 offset-xs4>
            <v-btn
              block
              :loading="loading"
              :disabled="loading"
              @click.native="getUserSignUp"
              class="primary white--text"
            >
              <v-icon dark large v-html="'mdi-fingerprint'"></v-icon>
              &nbsp;&nbsp;Sign Up
            </v-btn>
          </v-flex>
        </template>
        <template v-else>
          <v-flex xs4 offset-xs4>
            <br><h3 class="primary--text text-center">Direct to sign in view in a second.</h3>
          </v-flex>
        </template>
        <v-flex xs4 offset-xs4>
          <apps-messager 
            :message="message"
          ></apps-messager>
        </v-flex>
      </v-layout>
    </v-layout>
  </v-container>
</template>

<script>

  import { mapGetters, mapActions } from 'vuex'

  import Messager from '@/components/Messager'

  export default {
    name: 'apps-registration',
    components: {
      appsMessager: Messager,
    },
    data: () => (
      {
        // main
        title: 'Welcome!',
        signup: false,
        username: '',
        password: '',
        passwordConfirmed: '',
        loading: false,
        message: {
          content: '', 
          status: 'success',
          outlined: true,
        }
      }
    ),
    computed: {
      ...mapGetters({
        user: 'user',
      })
    },
    methods: {
      ...mapActions({
        setUser: 'setUser',
        registration: 'registration',
      }),
      async getUserSignUp () {
        this.loading = true
        this.registration({
          name: this.username, password: this.password, passwordConfirmed: this.passwordConfirmed
        }).then(
          // eslint-disable-next-line
          response => {
            this.username = ''
            this.password = ''
            this.passwordConfirmed = ''
            this.loading = false
            // return this.setUser(response)
            return response
          }
        ).then(
          // eslint-disable-next-line
          response => {
            this.title = 'Welcome Join ' + response.rows[0].name + '!'
            this.signup = true
            setTimeout(() => {
              this.$router.push({
                name: 'home' 
              })
            }, 3000)
          }
        ).catch(
          error => {
            this.loading = false
            this.message = {
              content: 'Sign up fail: ' + error.detail, status: 'error', outlined: true
            }
          }
        )
      },
    }
  }
</script>

